@import "../../style.scss";

#scaleContainer {
    padding: $density-spacings-extraSmall;
    background: #ffffff;
    border-radius: $basics-borderRadii-medium;
    box-shadow: 0px 2px 4px $colorScheme-lowShadowColor;
}

#scaleSquare {
    box-sizing: border-box;
    border: 1px solid $basics-colors-autodeskBlue200;
    background-color: white;
    display: inline-block;
    margin: 2px 5px 2px 0px;
    vertical-align: middle;
    max-width: 12px;
    max-height: 12px;
    padding: 0px;
}

figure {
    margin: 0px;
    padding: 0px;
}

figcaption {
    font-family: $typography-caption-fontFamily;
    font-size: $typography-body-fontSize;
    font-weight: $typography-caption-fontWeight;
    line-height: $typography-caption-lineHeight;
    color: $typography-caption-color;
    display: inline-block;
    vertical-align: middle;
}
